let debug = document.querySelector("#debug");
let submitButton = document.querySelectorAll("button[type=submit]")[0];
let image = document.querySelector("#photo img");
let form = document.querySelector("#pinterest-form");
let corsEnableButton = document.querySelector("#enable-cors");
let overlayBackground = document.querySelector("#overlay");
let closeModal = document.querySelector(".close-modal");
let allImages = [];
let imageIndex = 0;

let leftArrow = document.querySelector(".left.arrow");
let rightArrow = document.querySelector(".right.arrow");

corsEnableButton.onclick = function showOverlay(e) {
  overlayBackground.style.display = "block";
};

closeModal.onclick = function closeOverlay(e) {
  overlayBackground.style.display = "none";
};

leftArrow.onclick = (e) => {
  imageIndex--;
  if (imageIndex < 0) imageIndex = allImages.length - 1;
  console.log(imageIndex);
  console.log(allImages[imageIndex]);
  image.src = allImages[imageIndex];
};

rightArrow.onclick = (e) => {
  imageIndex++;
  if (imageIndex >= allImages.length) imageIndex = 0;
  console.log(imageIndex);
  console.log(allImages[imageIndex]);
  image.src = allImages[imageIndex];
};

form.addEventListener("submit", async function (e) {
  e.preventDefault();
  let pinterest_query = `/search/pins/?q=${form.elements[0].value}&rs=typed&term_meta[]=${form.elements[0].value}%7Ctyped`;
  let pinterest_queryURIEncode = encodeURIComponent(pinterest_query);
  let data = {
    options: {
      article: null,
      appliedProductFilters: "---",
      auto_correction_disabled: false,
      corpus: null,
      customized_rerank_type: null,
      filters: null,
      query: `${form.elements[0].value}`,
      query_pin_sigs: null,
      redux_normalize_feed: true,
      rs: "typed",
      scope: "pins",
      source_id: null,
      bookmarks: [
        ""
      ],
      no_fetch_context_on_resource: false
    },
    context: {}
  };
  let dataURI = encodeURIComponent(JSON.stringify(data));
  let URL = `https://cors-anywhere.herokuapp.com/https://www.pinterest.com/resource/BaseSearchResource/get/?source_url=${pinterest_queryURIEncode}&data=${dataURI}`;
  await fetch(URL, {
    method: "GET",
    headers: {
      "x-requested-with": "XMLHttpRequest",
      "x-pinterest-pws-handler": "www/search/[scope].js",
      "Content-Type": "application/x-www-form-urlencoded",
      "x-csrftoken": "0cdaafef9ea2ee92053c060e320cb4e8",
      accept: "application/json, text/javascript, */*, q=0.01",
      "x-pinterest-source-url": pinterest_query,
      cookie: `csrftoken=0cdaafef9ea2ee92053c060e320cb4e8; _routing_id="516fe14d-699b-4777-b23e-9921a6d3278b"; sessionFunnelEventLogged=1; _auth=1; _pinterest_; cm_sub=none; _pinterest_referrer=https://codesandbox.io/`
    }
  })
    .then((response) => {
      console.log(response);
      return response.json();
    })
    .then((json) => {
      console.log(json);
      let results = json["resource_response"]["data"]["results"];

      let imageKey;

      if (results[0]["images"]["orig"]) imageKey = "orig";
      else imageKey = Object.keys(results[0]["images"]).sort().pop();

      image.src = results[0]["images"][imageKey]["url"];
      allImages = [];
      for (let result of results) {
        allImages.push(result["images"][imageKey]["url"]);
      }
      console.log(allImages.length);
      alert("Done Fetching Images");
      debug.textContent = image.src;
      imageIndex = 0;
    })
    .catch((err) => {
      alert("Error! Fetch failed");
      debug.innerHTML = err;
    });

  //   await fetch(`https://www.pinterest.com/resource/BaseSearchResource/get/`, {
  //     method: "GET"
  //   })
  //     .then((response) => response.json())
  //     .then((json) => {
  //       console.log(json);
  //       let results = json["resource_response"]["data"]["results"];
  //       image.src = results[0]["images"]["orig"]["url"];
  //       allImages = [];
  //       for (let result of results) {
  //         allImages.push(result["images"]["orig"]["url"]);
  //       }
  //       console.log(allImages);
  //       alert("Done Image 2");
  //       debug.textContent = image.src;
  //     })
  //     .catch((err) => (debug.innerHTML = err));
});

